import { Color, CircleGeometry, MathUtils, MeshBasicMaterial, Raycaster, Matrix3, Mesh, Vector2, Vector3 } from 'three';
import { Component } from './Component';
import { BaseMovePointer, PointerState } from './BaseMovePointer';
import { OpenableDoor } from './OpenableDoor';

class VRMovePointer extends BaseMovePointer
{    
    constructor(domElement, scene, textureLoader)
    {
        super(scene, textureLoader);
        
        this.domElement = domElement;
        this.smoothedHitNormal = new Vector3();
        this.distanceToMovePoint = 0.0;
        this.cursorNotMovingTime = 0.0;
        this.moveActivateDelay = 2.5;
        this.lastHitPoint = null;

        // Pointer progress        
        var texture = textureLoader.load('images/move_pointer_progress.png');

        this.pointerProgressMaterial = new MeshBasicMaterial( { 
            map: texture,
            opacity: 1.0,
            color: new Color(1, 1, 0),
            transparent: true,
            depthTest: false
        } );

        var pointerProgressGeometry = new CircleGeometry(12.5, 40);
        this.pointerProgressObject = new Mesh(pointerProgressGeometry, this.pointerProgressMaterial);
        this.pointerProgressObject.renderOrder = 10002; // render after all other objects
        this.pointerProgressObject.visible = this.enabled;
        scene.add(this.pointerProgressObject);            
    }

    update(dt, camera)
    {
        super.update(dt, camera);

        this.pointsInteractable = false;
        this.hitObject = null;
        this.hitPoint = null;
        this.hitNormal = null;

        if (!this.enabled)
        {
            this.pointerObject.visible = false;
            this.pointerProgressObject.visible = false;
            this.pointerMaterial.opacity = 0.0;
            this.pointerState = PointerState.Hidden;            
            this.lastHitPoint = null;
            this.cursorNotMovingTime = 0.0;
            return;
        }

        this.pointerState = PointerState.Visible;

        var projCursorPosition = new Vector2(0, 0); // Cursor is always at the screen center.
        
        var raycaster = new Raycaster();
        raycaster.setFromCamera(projCursorPosition, camera);
        
        var hit;
        var intersects = raycaster.intersectObject(this.scene, true);                  
        for (var i=0; i<intersects.length; i++)
        {
            var iobj = intersects[i].object;
            if (iobj != this.pointerObject && iobj != this.pointerProgressObject)
            {
                hit = intersects[i];                
                break;
            }
        }

        if (!hit)
        {            
            this.pointerObject.visible = false;
            this.pointerProgressObject.visible = false;
            this.pointerMaterial.opacity = 0.0;
            this.smoothedHitNormal.set(0,0,0);
            this.lastHitPoint = null;
            this.cursorNotMovingTime = 0.0;
            return;
        }

        var normalMatrix = new Matrix3();
        normalMatrix.getNormalMatrix(hit.object.matrixWorld);        

        this.hitObject = hit.object;
        this.hitPoint = hit.point.clone();
        this.hitNormal = hit.face.normal.clone().applyMatrix3(normalMatrix);        
        this.hitNormal.normalize();

        if (Component.GetComponentInParent(this.hitObject, OpenableDoor.name))
        {
            this.pointerMaterial.map = this.openDoorTexture;
            this.pointsInteractable = true;
        }
        else
        {
            this.pointerMaterial.map = this.pointerTexture;            
        }

        if (this.smoothedHitNormal.lengthSq() == 0 || this.pointerMaterial.opacity < 0.05)
            this.smoothedHitNormal.copy(this.hitNormal);
        else
            this.smoothedHitNormal.lerpVectors(this.smoothedHitNormal.clone(), this.hitNormal, 0.3);
        this.smoothedHitNormal.normalize();

        this.pointerObject.position.copy(this.hitPoint).add(this.smoothedHitNormal);
        this.pointerObject.lookAt(this.hitPoint.clone().add(this.smoothedHitNormal.clone().multiplyScalar(2.0)));
        this.pointerObject.visible = true;

        this.pointerProgressObject.position.copy(this.pointerObject.position);
        this.pointerProgressObject.quaternion.copy(this.pointerObject.quaternion);        

        if (this.pointerState == PointerState.Visible)
            this.pointerMaterial.opacity += dt * 3.0;    // show
        else
            this.pointerMaterial.opacity -= dt * 3.0;
        this.pointerMaterial.opacity = MathUtils.clamp(this.pointerMaterial.opacity, 0.0, 1.0);            

        // Intersect with navmesh.
        var navMeshIntersects = [];
        this.navMesh.raycast(raycaster, navMeshIntersects);
      
        if (navMeshIntersects.length > 0 || this.pointsInteractable)
        {
            if (!this.lastHitPoint || this.cursorNotMovingTime == 0.0)
                this.lastHitPoint = this.hitPoint.clone(); 

            this.pointerMaterial.color = new Color(0.2, 1, 0.2);

            this.cursorNotMovingTime += dt;

            var dist = this.lastHitPoint.distanceTo(this.hitPoint);
            var radius = 25.0;

            if (dist > radius)
                this.cursorNotMovingTime = 0.0;            

            var progressShowDelay = this.moveActivateDelay * 0.3;
            if (this.cursorNotMovingTime >= progressShowDelay)
            {                                
                var progress = (this.cursorNotMovingTime - progressShowDelay) / (this.moveActivateDelay - progressShowDelay);
                var pointerProgressGeometry = new CircleGeometry(12.5, 40, 0, progress * Math.PI * 2);
                this.pointerProgressObject.geometry = pointerProgressGeometry;
                this.pointerProgressObject.visible = true;
            }
            else
                this.pointerProgressObject.visible = false;

            if (this.cursorNotMovingTime >= this.moveActivateDelay)
            {
                this.cursorNotMovingTime = 0.0;
                this.click();
            }
        }
        else
        {
            this.pointerMaterial.color = new Color(1, 1, 1);
            this.pointerProgressObject.visible = false;
            this.cursorNotMovingTime = 0.0;
        }
    }   

    click()
    {
        var door = Component.GetComponentInParent(this.hitObject, OpenableDoor.name);
        if (door)
        {
            door.openClose();
        }
        else
        {
            if (this.computeTargetMovePoint())
                this.domElement.dispatchEvent(this.moveEvent);
        }
    }
}

export { VRMovePointer };