import { BackSide, Color, ShaderChunk, UniformsUtils, UniformsLib } from 'three';

var ShaderLightmap = {

	uniforms: UniformsUtils.merge( [

		UniformsLib["common"],
		UniformsLib["envmap"],
		UniformsLib["aomap"],
		UniformsLib["lightmap"],
		UniformsLib["emissivemap"],
		UniformsLib["bumpmap"],
		UniformsLib["normalmap"],
		UniformsLib["displacementmap"],
		UniformsLib["roughnessmap"],
		UniformsLib["metalnessmap"],
		UniformsLib["fog"],
		UniformsLib["lights"],
		{
			emissive: { value: new Color( 0x000000 ) },
			roughness: { value: 0.5 },
			metalness: { value: 0.5 },
            envMapIntensity: { value: 1 }            
		}
    ]),    

    refreshUniforms: function (material)
    {        
        if (!material || !material.uniforms)
            return;

        var uniforms = material.uniforms;

        uniforms.map.value = material.map;        
        uniforms.diffuse.value = material.color;        
        uniforms.opacity.value = material.opacity;
        uniforms.aoMap.value = material.aoMap;
        uniforms.aoMapIntensity.value = material.aoMapIntensity;        
        uniforms.normalMap.value = material.normalMap;
        uniforms.normalScale.value = material.normalScale;
        uniforms.bumpMap.value = material.bumpMap;
        uniforms.bumpScale.value = material.bumpScale;
        if (material.side === BackSide)
            uniforms.bumpScale.value *= - 1;
        if (material.emissive !== undefined)
            uniforms.emissive.value = material.emissive;
        uniforms.emissiveMap.value = material.emissiveMap;
        uniforms.envMap.value = material.envMap;
        uniforms.envMapIntensity.value = material.envMapIntensity;        
        uniforms.lightMap.value = material.lightMap;
        uniforms.alphaMap.value = material.alphaMap;
        uniforms.roughnessMap.value = material.roughnessMap;        
        uniforms.roughness.value = material.roughness;
        uniforms.metalnessMap.value = material.metalnessMap;
        uniforms.metalness.value = material.metalness;        

        var uvScaleMap;
        if (material.map)
            uvScaleMap = material.map;
        else if (material.lightMap)
            uvScaleMap = material.lightMap;
        if (uvScaleMap)
        {
            uvScaleMap.updateMatrix();
            uniforms.uvTransform.value = uvScaleMap.matrix;
        }
    },

	fragmentShader: [

        "#define STANDARD",
        "#define PHYSICAL",
        "#define USE_UV",        

        "uniform vec3 diffuse;",
        "uniform vec3 emissive;",
        "uniform float roughness;",
        "uniform float metalness;",
        "uniform float opacity;",        

        "varying vec3 vViewPosition;",

        "#ifndef FLAT_SHADED",
	        "varying vec3 vNormal;",

            "#ifdef USE_TANGENT",
		        "varying vec3 vTangent;",
		        "varying vec3 vBitangent;",
	        "#endif",

        "#endif",

		ShaderChunk["common"],
        ShaderChunk["packing"],
        ShaderChunk["dithering_pars_fragment"],
        ShaderChunk["color_pars_fragment"],
        ShaderChunk["uv_pars_fragment"],
        ShaderChunk["uv2_pars_fragment"],
        ShaderChunk["map_pars_fragment"],
        ShaderChunk["alphamap_pars_fragment"],
        ShaderChunk["aomap_pars_fragment"],
        ShaderChunk["lightmap_pars_fragment"],
        ShaderChunk["emissivemap_pars_fragment"],
        ShaderChunk["bsdfs"],
        ShaderChunk["cube_uv_reflection_fragment"],        
        ShaderChunk["envmap_common_pars_fragment"],
        ShaderChunk["envmap_pars_fragment"],
        ShaderChunk["envmap_physical_pars_fragment"],
        ShaderChunk["fog_pars_fragment"],
        ShaderChunk["lights_pars_begin"],
        ShaderChunk["lights_physical_pars_fragment"],
        ShaderChunk["shadowmap_pars_fragment"],
        ShaderChunk["bumpmap_pars_fragment"],
        ShaderChunk["normalmap_pars_fragment"],
        ShaderChunk["roughnessmap_pars_fragment"],
        ShaderChunk["metalnessmap_pars_fragment"],
        ShaderChunk["logdepthbuf_pars_fragment"],
        ShaderChunk["clipping_planes_pars_fragment"],

		"void main() {",

	        ShaderChunk["clipping_planes_fragment"],

	        "vec4 diffuseColor = vec4( diffuse, opacity );",
	        "ReflectedLight reflectedLight = ReflectedLight( vec3( 0.0 ), vec3( 0.0 ), vec3( 0.0 ), vec3( 0.0 ) );",
	        "vec3 totalEmissiveRadiance = emissive;",

	        ShaderChunk["logdepthbuf_fragment"],
	        ShaderChunk["map_fragment"],
	        ShaderChunk["color_fragment"],
	        ShaderChunk["alphamap_fragment"],
	        ShaderChunk["alphatest_fragment"],
	        ShaderChunk["roughnessmap_fragment"],
	        ShaderChunk["metalnessmap_fragment"],
	        ShaderChunk["normal_fragment_begin"],
	        //ShaderChunk["normal_fragment_maps"],
            ShaderChunk["emissivemap_fragment"],
            
            "#if defined TANGENTSPACE_NORMALMAP", 
                "vec3 mapN;",
                "#if defined COMPRESSED_NORMALMAP",
                    "mapN.xy = texture2D(normalMap, vUv).wy * 2.0 - 1.0;",
                    "mapN.z = sqrt(1.0 - dot(mapN.xy, mapN.xy));",
                "#else",
                    "mapN = texture2D(normalMap, vUv).xyz * 2.0 - 1.0;",
                "#endif",

                "mapN.xy *= normalScale;",

                "#ifdef USE_TANGENT",
                    "normal = normalize(vTBN * mapN);",
                "#else",
                    "normal = perturbNormal2Arb( -vViewPosition, normal, mapN );",
                "#endif",
            "#elif defined USE_BUMPMAP",
                "normal = perturbNormalArb( -vViewPosition, normal, dHdxy_fwd() );",
            "#endif",
           
            "#ifdef BAKED_LIGHT_ONLY",

                "PhysicalMaterial material;",
                "material.diffuseColor = diffuseColor.rgb * ( 1.0 - metalnessFactor );",
                "material.specularRoughness = clamp( roughnessFactor, 0.04, 1.0 );",                            
                "material.specularColor = mix( vec3( DEFAULT_SPECULAR_COEFFICIENT ), diffuseColor.rgb, metalnessFactor );",   

                "GeometricContext geometry;",
                "geometry.position = -vViewPosition;",
                "geometry.normal = normal;",
                "geometry.viewDir = normalize(vViewPosition);",

                "IncidentLight directLight;",

                "vec3 irradiance = vec3(0.0);",
                "vec3 radiance = vec3(0.0);",

                "#if defined (USE_UV1_FOR_LIGHTMAP)",
                    "vec4 lightMapTexel = texture2D(lightMap, vUv);",
                "#else",
                    "vec4 lightMapTexel = texture2D(lightMap, vUv2);",
                "#endif",

                "irradiance += lightMapTexelToLinear(lightMapTexel).rgb * material.diffuseColor * lightMapIntensity;",

                "#if defined (USE_ENVMAP)",
                    "radiance += getLightProbeIndirectRadiance(geometry.viewDir, geometry.normal, material.specularRoughness, maxMipLevel);",
                "#endif",
                
                // Both indirect specular and indirect diffuse light accumulate here
                "reflectedLight.indirectDiffuse += irradiance;",
                "reflectedLight.indirectSpecular += radiance * BRDF_Specular_GGX_Environment(geometry.viewDir, geometry.normal, material.specularColor, material.specularRoughness );",

                // modulation
                //ShaderChunk["aomap_fragment"],
                            
                "#ifdef USE_AOMAP",
                    // reads channel R, compatible with a combined OcclusionRoughnessMetallic (RGB) texture
                    "float ambientOcclusion = ( texture2D( aoMap, vUv2 ).r - 1.0 ) * aoMapIntensity + 1.0;",
                    "#if defined( USE_ENVMAP ) && defined( STANDARD )",
                        "float dotNV = saturate( dot( geometry.normal, geometry.viewDir ) );",
                        "reflectedLight.indirectSpecular *= computeSpecularOcclusion( dotNV, ambientOcclusion, material.specularRoughness );",
                    "#endif",
                "#endif",

            "#else",

                // accumulation
                "#include <lights_physical_fragment>",
                "#include <lights_fragment_begin>",
                "#include <lights_fragment_maps>",
                
                //"RE_IndirectDiffuse(irradiance, geometry, material, reflectedLight);",
                //"RE_IndirectSpecular(radiance, iblIrradiance, clearcoatRadiance, geometry, material, reflectedLight);",

                // Both indirect specular and indirect diffuse light accumulate here          
                "vec3 singleScattering = vec3(0.0);",
                "vec3 multiScattering = vec3(0.0);",
                "vec3 cosineWeightedIrradiance = iblIrradiance * RECIPROCAL_PI;",
            
                "BRDF_Specular_Multiscattering_Environment( geometry, material.specularColor, material.specularRoughness, singleScattering, multiScattering );",

                "vec3 diffuse = material.diffuseColor * ( 1.0 - ( singleScattering + multiScattering ) );",
                "reflectedLight.indirectSpecular += radiance * singleScattering;",
                "reflectedLight.indirectSpecular += multiScattering * cosineWeightedIrradiance;",

                "reflectedLight.indirectDiffuse += irradiance * BRDF_Diffuse_Lambert(material.diffuseColor);",                

                // modulation
                "#include <aomap_fragment>",

                // this is a stub for the transparency model
                "#ifdef TRANSPARENCY",
                "    diffuseColor.a *= saturate( 1. - transparency + linearToRelativeLuminance( reflectedLight.directSpecular + reflectedLight.indirectSpecular ) );",
                "#endif",
            
            "#endif",

            "vec3 outgoingLight = reflectedLight.directDiffuse + reflectedLight.indirectDiffuse + reflectedLight.directSpecular + reflectedLight.indirectSpecular + totalEmissiveRadiance;",            
            //"vec3 outgoingLight = reflectedLight.indirectSpecular;",
  
            "gl_FragColor = vec4(outgoingLight, diffuseColor.a);",
            
            //"#if defined TANGENTSPACE_NORMALMAP",
            //    "gl_FragColor = vec4(mapN.xyz, diffuseColor.a);",
            //"#endif",
                   
            ShaderChunk["tonemapping_fragment"],
            ShaderChunk["encodings_fragment"],
            ShaderChunk["fog_fragment"],
            ShaderChunk["premultiplied_alpha_fragment"],
            ShaderChunk["dithering_fragment"],

		"}"

	].join( "\n" ),

	vertexShader: [

        "#define PHYSICAL",
        "#define USE_LIGHTMAP",
        "#define USE_UV",

        "varying vec3 vViewPosition;",

        "#ifndef FLAT_SHADED",

	        "varying vec3 vNormal;",

            "#ifdef USE_TANGENT",
		        "varying vec3 vTangent;",
                "varying vec3 vBitangent;",
            "#endif",

        "#endif",

        ShaderChunk["common"],

        ShaderChunk["uv_pars_vertex"],
        ShaderChunk["uv2_pars_vertex"],

        ShaderChunk["displacementmap_pars_vertex"],
        ShaderChunk["color_pars_vertex"],
        ShaderChunk["fog_pars_vertex"],
        ShaderChunk["morphtarget_pars_vertex"],
        ShaderChunk["skinning_pars_vertex"],
        ShaderChunk["shadowmap_pars_vertex"],
        ShaderChunk["logdepthbuf_pars_vertex"],
        ShaderChunk["clipping_planes_pars_vertex"],	        

		"void main() {",

	        ShaderChunk["uv_vertex"],
	        ShaderChunk["uv2_vertex"],
	        ShaderChunk["color_vertex"],

	        ShaderChunk["beginnormal_vertex"],
	        ShaderChunk["morphnormal_vertex"],
	        ShaderChunk["skinbase_vertex"],
	        ShaderChunk["skinnormal_vertex"],
	        ShaderChunk["defaultnormal_vertex"],

            "#ifndef FLAT_SHADED", // Normal computed with derivatives when FLAT_SHADED

	            "vNormal = normalize( transformedNormal );",

	            "#ifdef USE_TANGENT",

		            "vTangent = normalize( transformedTangent );",
		            "vBitangent = normalize( cross( vNormal, vTangent ) * tangent.w );",

	            "#endif",

            "#endif",

	        ShaderChunk["begin_vertex"],
	        ShaderChunk["morphtarget_vertex"],
	        ShaderChunk["skinning_vertex"],
	        ShaderChunk["displacementmap_vertex"],
	        ShaderChunk["project_vertex"],
	        ShaderChunk["logdepthbuf_vertex"],
	        ShaderChunk["clipping_planes_vertex"],

	        "vViewPosition = - mvPosition.xyz;",

	        ShaderChunk["worldpos_vertex"],
	        ShaderChunk["shadowmap_vertex"],
	        ShaderChunk["fog_vertex"],

		"}"

	].join( "\n" )

};

export { ShaderLightmap };