import { MathUtils, Raycaster, Vector3 } from 'three';

import { Component } from './Component';
import { playerInstance } from './Player';

var RobotState = {
    Move: 0,
    Rotate: 1
}

class VacuumCleanerRobot extends Component
{    
    constructor(parameters)
    {
        super();                                   
    }

    start()
    {
        this.state = RobotState.Move;
        this.currentAngle = 0.0;
        this.destAngle = 0.0;
        this.initialRotation = this.sceneObject.quaternion.clone();    
    }

    update(dt)
    {
        var moveSpeed = 7;
        var rotateSpeed = 2;    // rad/s        

        if (this.state == RobotState.Move)
        {
            var e = this.sceneObject.matrixWorld.elements;
            var forward = new Vector3();
            forward.set(e[4], e[5], e[6]).negate();
        
            var newPosition = this.sceneObject.position.clone();        
            var move = forward.clone().multiplyScalar(moveSpeed * dt);
            newPosition.add(move);

            var canMove = this.canMoveToPosition(newPosition);

            if (canMove)
            {
                this.sceneObject.position.add(move);
            }
            else
            {
                this.state = RobotState.Rotate;
                this.destAngle = MathUtils.randFloat(0, Math.PI * 2);
            }
        }
        
        if (this.state == RobotState.Rotate)
        {
            if (this.destAngle >= this.currentAngle)
            {
                this.currentAngle += rotateSpeed * dt;
                if (this.currentAngle >= this.destAngle)
                {
                    this.currentAngle = this.destAngle;
                    this.state = RobotState.Move;
                }
            }
            else
            {
                this.currentAngle -= rotateSpeed * dt;
                if (this.currentAngle <= this.destAngle)
                {
                    this.currentAngle = this.destAngle;
                    this.state = RobotState.Move;
                }
            }            

            this.sceneObject.setRotationFromQuaternion(this.initialRotation);
            this.sceneObject.rotateZ(this.currentAngle);            
        }
    }

    canMoveToPosition(position)
    {
        var navMesh = playerInstance.getNavMesh();
        if (!navMesh)
            return false;

        // Трассировка вертикального луча для поиска пересечения с navmesh.
        var raycaster = new Raycaster();
        raycaster.set(position.clone().add(new Vector3(0,10000,0)), new Vector3(0,-1,0));
        var navMeshIntersects = [];
        navMesh.raycast(raycaster, navMeshIntersects);

        return navMeshIntersects.length > 0;
    }
}

export { VacuumCleanerRobot };