import { MathUtils } from 'three';
import { Component } from './Component'

class OpenableDoor extends Component
{
    constructor(parameters)
    {
        super();                   
        this.maxAngle = parameters.maxAngle ? MathUtils.degToRad(parameters.maxAngle) : MathUtils.degToRad(90);
    }

    start()
    {
        this.quat = this.sceneObject.quaternion.clone();
        this.opened = false;
    }

    update(dt)
    {
    }

    openClose()
    {
        // Restore rotation
        this.sceneObject.setRotationFromQuaternion(this.quat);
        
        if (!this.opened)
        {
            this.sceneObject.rotateZ(-this.maxAngle);
            this.opened = true;
        }
        else
            this.opened = false;
    }
}

export { OpenableDoor };