import BspBuildWorker from 'worker-loader!./BspBuildWorker.js';

import { Mesh } from "three";

export default class BspBuildWorkerWrapper {

	constructor (onmessage) {

		this.objects = [];
		this.sceneObjectArray = [];
		this.handle = new BspBuildWorker();
		this.handle.onmessage = onmessage;
	}

	makeWorkerObject (object) {

		if (

			object instanceof Mesh &&
			object.geometry &&
			object.geometry.isBufferGeometry
		) {

				const sceneObjectArrayIndex = this.sceneObjectArray.length;

				this.sceneObjectArray.push(object);

				this.objects.push({
					matrixWorld: object.matrixWorld.elements,
					materialCount: object.material.length || 0,
					sceneObjectArrayIndex,
					position: object.geometry.attributes.position.array,
					index: object.geometry.index ? object.geometry.index.array : null,
					drawRange: object.geometry.drawRange,
					groups: object.geometry.groups,
					name: object.name,
				});
		}

		if (object.children.length) {

			object.children.forEach((child) => this.makeWorkerObject(child));
		}
	}

	replaceIndicesByObjects (rootNode) {

		const iterate = (node) => {

			for (const key in node) {

				if (key === 'faces') {

					node[key].forEach((face) => (face.object = this.sceneObjectArray[face.sceneObjectArrayIndex]));
				}
				else if (key !== 'plane' && node[key]) {

					iterate(node[key], Object.keys(node[key]));
				}
			}
		};

		iterate(rootNode);
	}
}
