import { BackSide, Color, UniformsUtils, UniformsLib } from 'three';
import PhysicalMaterial_PS from './PhysicalMaterial_PS.glsl';
import PhysicalMaterial_VS from './PhysicalMaterial_VS.glsl';

var PhysicalMaterialShader = {

	uniforms: UniformsUtils.merge( [

		UniformsLib["common"],
		UniformsLib["envmap"],
		UniformsLib["aomap"],
		UniformsLib["lightmap"],
		UniformsLib["emissivemap"],
		UniformsLib["bumpmap"],
		UniformsLib["normalmap"],
		UniformsLib["displacementmap"],
		UniformsLib["roughnessmap"],
		UniformsLib["metalnessmap"],
		UniformsLib["fog"],
		UniformsLib["lights"],
		{
			emissive: { value: new Color( 0x000000 ) },
			roughness: { value: 0.5 },
            metalness: { value: 0.5 },            
            envMapIntensity: { value: 1 },            
            roughnessMapMin: { value: 0 },
            roughnessMapMax: { value: 1 }
			//transparency: { value: 0 },
			//clearcoat: { value: 0 },
			//clearcoatRoughness: { value: 0 },
			//sheen: { value: new Color( 0x000000 ) },
			//clearcoatNormalScale: { value: new Vector2( 1, 1 ) },
			//clearcoatNormalMap: { value: null },            
		}
    ]),    

    refreshUniforms: function (material)
    {        
        if (!material || !material.uniforms)
            return;

        var uniforms = material.uniforms;

        uniforms.map.value = material.map;
        uniforms.diffuse.value = material.color;

        uniforms.reflectivity.value = material.reflectivity;

        uniforms.opacity.value = material.opacity;
        uniforms.aoMap.value = material.aoMap;
        uniforms.aoMapIntensity.value = material.aoMapIntensity;
        
        uniforms.normalMap.value = material.normalMap;
        uniforms.normalScale.value = material.normalScale;
        uniforms.bumpMap.value = material.bumpMap;
        uniforms.bumpScale.value = material.bumpScale;
        if (material.side === BackSide)
            uniforms.bumpScale.value *= - 1;
        
        uniforms.emissive.value = material.emissive;
        uniforms.emissiveMap.value = material.emissiveMap;

        uniforms.envMap.value = material.envMap;
        uniforms.envMapIntensity.value = material.envMapIntensity;

        uniforms.lightMap.value = material.lightMap;
        uniforms.lightMapIntensity.value = material.lightMapIntensity;        
        
        uniforms.alphaMap.value = material.alphaMap;
        
        uniforms.roughnessMap.value = material.roughnessMap;
        uniforms.roughnessMapMin.value = material.roughnessMapMin;
        uniforms.roughnessMapMax.value = material.roughnessMapMax;
        uniforms.roughness.value = material.roughness;

        uniforms.metalnessMap.value = material.metalnessMap;
        uniforms.metalness.value = material.metalness;        

        var uvScaleMap;
        if (material.map)
            uvScaleMap = material.map;
        else if (material.lightMap)
            uvScaleMap = material.lightMap;
        if (uvScaleMap)
        {
            uvScaleMap.updateMatrix();
            uniforms.uvTransform.value = uvScaleMap.matrix;
        }

        if (uniforms.diffuse.value === undefined)
            throw new Error("uniforms.diffuse.value is undefined");
        if (uniforms.reflectivity.value === undefined)
            throw new Error("uniforms.reflectivity.value is undefined");
        if (uniforms.opacity.value === undefined)
            throw new Error("uniforms.opacity.value is undefined");            
        if (uniforms.aoMapIntensity.value === undefined)
            throw new Error("uniforms.aoMapIntensity.value is undefined");            
        if (uniforms.normalScale.value === undefined)
            throw new Error("uniforms.normalScale.value is undefined");
        if (uniforms.bumpScale.value === undefined)
            throw new Error("uniforms.bumpScale.value is undefined");
        if (uniforms.emissive.value === undefined)
            throw new Error("uniforms.emissive.value is undefined");
        if (uniforms.envMapIntensity.value === undefined)
            throw new Error("uniforms.envMapIntensity.value is undefined");
        if (uniforms.lightMapIntensity.value === undefined)
            throw new Error("uniforms.lightMapIntensity.value is undefined");                        
        if (uniforms.roughnessMapMin.value === undefined)
            throw new Error("uniforms.roughnessMapMin.value is undefined");
        if (uniforms.roughnessMapMax.value === undefined)
            throw new Error("uniforms.roughnessMapMax.value is undefined");
        if (uniforms.roughness.value === undefined)
            throw new Error("uniforms.roughness.value is undefined");
        if (uniforms.metalness.value === undefined)
            throw new Error("uniforms.metalness.value is undefined");
    },

	fragmentShader: PhysicalMaterial_PS,

	vertexShader: PhysicalMaterial_VS
};

export { PhysicalMaterialShader };