import { ShaderMaterial, Mesh } from 'three';
import { PhysicalMaterialShader } from './shaders/PhysicalMaterialShader';
import { ShaderLightmap } from './shaders/ShaderLightmap';

class SceneUtils 
{
    static getObjectCustomType(obj, useParentType) {
        while (obj) {
            if (obj.userData === undefined || obj.userData.customProperties === undefined || obj.userData.customProperties.type === undefined) {
                if (useParentType) {
                    obj = obj.parent;
                    continue;
                }
                else
                    return undefined;
            }

            return obj.userData.customProperties.type;
        }

        return undefined;
    }

    static isObjectHiddenInOverviewMode(obj) {
        return obj.userData !== undefined && obj.userData.customProperties !== undefined && obj.userData.customProperties.hideInOverviewMode;
    }

    static isObjectHiddenInPanormaicMode(obj) {
        return obj.userData !== undefined && obj.userData.customProperties !== undefined && obj.userData.customProperties.hideInPanoramicMode;
    }        

    static isObjectHideable(obj) {
        return obj.userData !== undefined && obj.userData.customProperties !== undefined && obj.userData.customProperties.hideableObject;
    }

    static getObjectBackAxis(obj) {
        if (obj.userData !== undefined && obj.userData.customProperties !== undefined && obj.userData.customProperties.backAxis !== undefined)
            return obj.userData.customProperties.backAxis;
        else
            return "x";
    }    

    static isHideAllowedForObject(obj) {
        return SceneUtils.isObjectHiddenInOverviewMode(obj) || SceneUtils.isObjectHiddenInPanormaicMode(obj) || SceneUtils.isObjectHideable(obj);
    }

    static isHideAllowedForParentObject(obj) {            
        var parent = obj.parent;
        while (parent) {
            if (SceneUtils.isHideAllowedForObject(parent))
                return true;
            parent = parent.parent;
        }

        return false;
    }        

    static smartCloneMaterial(mtl) {
        var out = mtl.clone();

        if (mtl.initialOpacity !== undefined)
            out.initialOpacity = mtl.initialOpacity;

        if (out instanceof ShaderMaterial) {
            if (out.fragmentShader == ShaderLightmap.fragmentShader || out.fragmentShader == PhysicalMaterialShader.fragmentShader) {
                out.map = mtl.map;
                out.color = mtl.color;
                out.opacity = mtl.opacity;
                out.aoMap = mtl.aoMap;
                out.aoMapIntensity = mtl.aoMapIntensity;
                out.alphaMap = mtl.alphaMap;
                out.alphaTest = mtl.alphaTest;
                out.lightMap = mtl.lightMap;
                out.lightMapIntensity = mtl.lightMapIntensity;
                out.normalMap = mtl.normalMap;            
                out.normalScale = mtl.normalScale;
                out.normalMapType = mtl.normalMapType;
                out.bumpMap = mtl.bumpMap;
                out.bumpScale = mtl.bumpScale;
                out.envMap = mtl.envMap;
                out.envMapIntensity = mtl.envMapIntensity;                
                out.metalnessMap = mtl.metalnessMap;
                out.metalness = mtl.metalness;
                out.roughnessMap = mtl.roughnessMap;
                out.roughness = mtl.roughness;
                out.defines = mtl.defines;
            }        

            if (out.fragmentShader == PhysicalMaterialShader.fragmentShader) {                
                out.emissive = mtl.emissive;
                out.emissiveMap = mtl.emissiveMap;
                out.roughnessMapMin = mtl.roughnessMapMin;
                out.roughnessMapMax = mtl.roughnessMapMax;
                out.reflectivity = mtl.reflectivity;
                PhysicalMaterialShader.refreshUniforms(out);               
            }                
            else if (out.fragmentShader == ShaderLightmap.fragmentShader)
                ShaderLightmap.refreshUniforms(out);               
        }

        return out;
    }

    static cloneMaterialsForHideableObjects(root) {
        root.traverse(function (obj) {
            var hideable = SceneUtils.isHideAllowedForObject(obj);
            var parentIsHideable = SceneUtils.isHideAllowedForParentObject(obj);

            if (obj instanceof Mesh && (hideable || parentIsHideable)) {
                if (Array.isArray(obj.material)) {
                    for (var i = 0; i < obj.material.length; i++) {
                        obj.material[i] = SceneUtils.smartCloneMaterial(obj.material[i]);
                        obj.material[i].initialOpacity = obj.material[i].opacity;
                    }
                }
                else {
                    obj.material = SceneUtils.smartCloneMaterial(obj.material);
                    obj.material.initialOpacity = obj.material.opacity;
                }
            }
        });
    }    

    static shiftObjectOpacityToValue(opacity, obj, speed) {
        if (speed <= 0)
            return;

        var materials = [];
        if (Array.isArray(obj.material))
            materials = obj.material;
        else
            materials.push(obj.material);

        materials.forEach(function (mtl) {
            var currentOpacity = mtl.opacity;
            if (mtl.initialOpacity !== undefined)
                currentOpacity = mtl.opacity * mtl.initialOpacity;      
                
            if (opacity <= currentOpacity) {
                currentOpacity -= speed;
                if (currentOpacity < opacity)
                    currentOpacity = opacity;
            }
            else {
                currentOpacity += speed;
                if (currentOpacity > opacity)
                    currentOpacity = opacity;
            }                

            mtl.opacity = currentOpacity;

            mtl.transparent = mtl.opacity < 1 ? true : false;
            if (mtl.uniforms)
                mtl.uniforms["opacity"].value = mtl.opacity;
        });
    }

    static setObjectOpacity(opacity, obj) {
        var materials = [];
        if (Array.isArray(obj.material))
            materials = obj.material;
        else
            materials.push(obj.material);

        materials.forEach(function (mtl) {
            mtl.opacity = opacity;
            if (mtl.initialOpacity !== undefined)
                mtl.opacity = mtl.opacity * mtl.initialOpacity;

            mtl.transparent = mtl.opacity < 1 ? true : false;
            if (mtl.uniforms)
                mtl.uniforms["opacity"].value = mtl.opacity;
        });
    }

    static FixupObsoleteObjectProperties(properties) {
        // 'HideableWall' property is obsolete, 'hideableObject' must be used instead.
        if (properties.HideableWall !== undefined)
            properties.hideableObject = properties.HideableWall;			
        
        if (properties.cameraType == "panoramic")
            properties.cameraType = "freelook";
        else if (properties.cameraType == "main")
            properties.cameraType = "orbit";
    }
};

export { SceneUtils };