import { Cache, Loader } from "three";
import { gunzip, unzip } from 'fflate';

const loading = {};

function CompressedFileLoader( manager, deflateLib ) {
    Loader.call( this, manager );
    this.deflateLib = deflateLib !== undefined ? deflateLib : "gz"; // "zip" or "gz", default is gz
}

CompressedFileLoader.prototype = Object.assign( Object.create( Loader.prototype ), {

	constructor: CompressedFileLoader,

	load: function (url, onLoad, onProgress, onError) {

        if (url === undefined) 
            url = '';

        // Add archive extension
        url += "." + this.deflateLib;

        if (this.path !== undefined) 
            url = this.path + url;

		url = this.manager.resolveURL( url );

		const scope = this;

		const cached = Cache.get(url);

		if (cached !== undefined) {
			scope.manager.itemStart( url );

			setTimeout(function () {
                if (onLoad)
                    onLoad( cached );
				scope.manager.itemEnd( url );
			}, 0 );

			return cached;
		}

		// Check if request is duplicate

		if ( loading[ url ] !== undefined ) {

			loading[ url ].push({
				onLoad: onLoad,
				onProgress: onProgress,
				onError: onError
			});

			return;
		}

		let request;

        // Initialise array for duplicate requests

        loading[ url ] = [];

        loading[ url ].push( {

            onLoad: onLoad,
            onProgress: onProgress,
            onError: onError

        } );

        request = new XMLHttpRequest();

        request.open( 'GET', url, true );

        request.addEventListener( 'load', async function ( event ) {

            const response = this.response;

            const callbacks = loading[ url ];

            delete loading[ url ];

            if ( this.status === 200 || this.status === 0 ) {

                // Some browsers return HTTP Status 0 when using non-http protocol
                // e.g. 'file://' or 'data://'. Handle as success.

                if (this.status === 0) 
                    console.warn( 'CompressedFileLoader: HTTP Status 0 received.' );
                
                var onDecompressed = function(error, data) {                                        
                    if (data) {
                        var buffer;
                        if (data instanceof Uint8Array) {
                            buffer = data.buffer;
                        }
                        else {
                            var entries = Object.entries(data);
                            buffer = entries[0][1].buffer;
                        }       

                        // Add to cache only on HTTP success, so that we do not cache
                        // error response bodies as proper responses to requests.
                        Cache.add(url, buffer);
                                        
                        for (let i = 0, il = callbacks.length; i < il; i ++) {

                            const callback = callbacks[ i ];
                            if (callback.onLoad) 
                                callback.onLoad(buffer);
                        }

                        scope.manager.itemEnd(url);
                    }
                    else {
                        console.warn("Error decompressing data with fflate", error)
                        for ( let i = 0, il = callbacks.length; i < il; i ++ ) {
                            const callback = callbacks[ i ];
                            if (callback.onError)
                                callback.onError(event);
                        }

                        scope.manager.itemError( url );
                        scope.manager.itemEnd( url );
                    }
                };

                if (scope.deflateLib == "zip") { 
                    // Decompress the response using zip.
                    unzip(new Uint8Array(response), onDecompressed);
                }
                else { 
                    // Decompress the response using gunzip.
                    gunzip(new Uint8Array(response), onDecompressed);
                }

            } else {
                for ( let i = 0, il = callbacks.length; i < il; i ++ ) {

                    const callback = callbacks[ i ];
                    if (callback.onError) 
                        callback.onError( event );

                }

                scope.manager.itemError( url );
                scope.manager.itemEnd( url );
            }

        }, false );

        request.addEventListener( 'progress', function ( event ) {

            const callbacks = loading[ url ];

            for ( let i = 0, il = callbacks.length; i < il; i ++ ) {

                const callback = callbacks[ i ];
                if ( callback.onProgress ) callback.onProgress( event );

            }

        }, false );

        request.addEventListener( 'error', function ( event ) {

            const callbacks = loading[ url ];

            delete loading[ url ];

            for ( let i = 0, il = callbacks.length; i < il; i ++ ) {

                const callback = callbacks[ i ];
                if ( callback.onError ) callback.onError( event );

            }

            scope.manager.itemError( url );
            scope.manager.itemEnd( url );

        }, false );

        request.addEventListener( 'abort', function ( event ) {

            const callbacks = loading[ url ];

            delete loading[ url ];

            for ( let i = 0, il = callbacks.length; i < il; i ++ ) {
                const callback = callbacks[ i ];
                if ( callback.onError ) callback.onError( event );
            }

            scope.manager.itemError( url );
            scope.manager.itemEnd( url );

        }, false );

        if ( this.responseType !== undefined ) request.responseType = this.responseType;
        if ( this.withCredentials !== undefined ) request.withCredentials = this.withCredentials;

        if ( request.overrideMimeType ) request.overrideMimeType( this.mimeType !== undefined ? this.mimeType : 'text/plain' );

        for ( const header in this.requestHeader ) {
            request.setRequestHeader( header, this.requestHeader[ header ] );
        }

        request.send( null );

		scope.manager.itemStart( url );

		return request;

	},

	setResponseType: function ( value ) {

		this.responseType = value;
		return this;

	},

	setMimeType: function ( value ) {

		this.mimeType = value;
		return this;

	}

} );


export { CompressedFileLoader };