import {
	ClampToEdgeWrapping,
	DefaultLoadingManager,	
    ImageLoader,    
    LinearFilter,
    RGBAFormat,
    RGBFormat,
	Texture,
    VideoTexture
} from "three";
import { KTXLoader } from "three/examples/jsm/loaders/KTXLoader.js";
import { DDSLoader } from "./DDSLoader";
import { PVRLoader } from "three/examples/jsm/loaders/PVRLoader.js";
import { CompressedFileLoader } from "./CompressedFileLoader";

function CompressedTextureLoader(manager, s3tc, etc1, pvrtc, astc, inflateLib) {
    this.manager = ( manager !== undefined ) ? manager : DefaultLoadingManager;
    
    this.compressedFileLoader = new CompressedFileLoader(manager, inflateLib);
    this.compressedFileLoader.setResponseType('arraybuffer');

    this.textureByFilenameMap = new Map();

    this.s3tcSupported = s3tc;
    this.etc1Supported = etc1;
    this.pvrtcSupported = pvrtc;
    this.astcSupported = astc;    
    this.compressedTexturesEnabled = true;

    if (s3tc)
        this.preferedFormat = 's3tc';
    else if (astc)
        this.preferedFormat = 'astc';
    else if (etc1)
        this.preferedFormat = 'etc1';
    else if (pvrtc)
        this.preferedFormat = 'pvrtc';
    else {
        this.preferedFormat = '';
        console.error('Texture compression is not supported!');
    }
}

Object.assign(CompressedTextureLoader.prototype, {
    textureLodToLoad: 2,
    crossOrigin: 'anonymous',

    setEnableCompressedTextures: function (value) {
        this.compressedTexturesEnabled = value;        
    },

    loadVideoTexture: function (url, onLoad, onProgress, onError) {
        //playsinline loop muted autoplay
        var video = document.createElement('video');
        video.muted = true;
        video.loop = true;
        video.playsInline = true;
        video.autoplay = true;
        video.src = url;
        video.load();
        video.play();

        var texture = new VideoTexture(video);
        texture.wrapS = ClampToEdgeWrapping;
        texture.wrapT = ClampToEdgeWrapping;
        texture.minFilter = LinearFilter;

        if (onLoad !== undefined)
            onLoad(texture);

        return texture;
    },

    load: function (url, onLoad, onProgress, onError) {
        
        if (url === undefined) {
            if (onError !== undefined)
                onError(new Error("url is undefined."));
        }

        // Игнорируем ссылки такого вида, которые могут быть в gltf файле.
        if (url.startsWith("data:image")) {
            if (onLoad !== undefined)
                onLoad(null);
            return null;
        }

        // Get from texture cache if possible.
        var cachedTexture = this.textureByFilenameMap.get(url);
        if (cachedTexture !== undefined) {
            if (onLoad !== undefined)
                onLoad(cachedTexture);
            return cachedTexture;
        }

        // Special case: if the name starts from VT_ - it is a video texture.
        if (url.toLowerCase().indexOf('vt_') != -1) {
            // Change the extension to .mp4
            url = url.substr(0, url.lastIndexOf(".")) + ".mp4";
            return this.loadVideoTexture(url, onLoad, onProgress, onError);
        }

        var texture = new Texture();
        texture.anisotropy = 4;

        this.textureByFilenameMap.set(url, texture);         

        // 'images' folder contains uncompressed images
        if (!this.compressedTexturesEnabled || url.startsWith('images/')) {
            var loader = new ImageLoader(this.manager);
            loader.setCrossOrigin(this.crossOrigin);
            loader.setPath(this.path);

            loader.load(url, function (image) {
                texture.image = image;

                // JPEGs can't have an alpha channel, so memory can be saved by storing them as RGB.
                var isJPEG = url.search( /\.jpe?g($|\?)/i ) > 0 || url.search( /^data\:image\/jpeg/ ) === 0;

                texture.format = isJPEG ? RGBFormat : RGBAFormat;
                texture.needsUpdate = true;

                if (onLoad !== undefined)
                    onLoad(texture);

            }, 
            onProgress,
            function(error) {
                if (onError)
                    onError(error)
                console.warn("Failed to load texture: " + url, error);
            });
        }       
        else {        
            // Load compressed texture
            //console.log('Loading texture: ' + lodUrl + "...");
            this.loadCompressedTexture(url, this.textureLodToLoad, texture, this.manager, this.preferedFormat, onLoad, onProgress, onError);            
        }

		return texture;

	},

    getLODUrl: function (url, lod) {
        var slashIndex = url.lastIndexOf("/");
        var filePath = url.substring(0, slashIndex + 1);
        var fileName = url.substring(slashIndex + 1);
        var dotIndex = fileName.lastIndexOf(".");
        var fileNameWithoutExt = fileName.substring(0, dotIndex);

        var compressedTextureUrl;
                
        switch (this.preferedFormat) {
            case 's3tc':
                compressedTextureUrl = filePath + 's3tc/' + lod + '/' + fileNameWithoutExt + ".dds";
                break;
            case 'etc1':
                compressedTextureUrl = filePath + 'etc1/' + lod + '/' + fileNameWithoutExt + ".ktx";
                break;
            case 'pvrtc':
                compressedTextureUrl = filePath + 'pvrtc/' + lod + '/' + fileNameWithoutExt + ".pvr";
                break;
            case 'astc':
                compressedTextureUrl = filePath + 'astc/' + lod + '/' + fileNameWithoutExt + ".ktx";
                break;
            default:
                compressedTextureUrl = url;
        }

        return compressedTextureUrl;
    },

    loadCompressedTexture: function (url, lod, texture, manager, format, onLoad, onProgress, onError) {
        var self = this;

        if (this.preferedFormat === undefined || this.preferedFormat == '') {
            if (onError !== undefined)
                onError(new Error("S3TC, ETC1, ASTC or PVRTC textures are not supported."));
            return;
        }
            
        var compressedUrl = this.getLODUrl(url, lod);

        this.compressedFileLoader.load(compressedUrl,
            async function (buffer) {
                // Use loader object as parser
                var texLoader;
                switch (format) {
                    case 's3tc':
                        texLoader = new DDSLoader();
                        break;
                    case 'etc1':
                    case 'astc':
                        texLoader = new KTXLoader();
                        break;
                    case 'pvrtc':
                        texLoader = new PVRLoader();
                        break;
                    default:     
                        throw new Error("compressed texture format is not set.");
                }

                if (texLoader) {
                    var texData = texLoader.parse(buffer, true);

                    if (texData.isCubemap) {
                        if (onError !== undefined)
                            onError(new Error('Cube map is not supported.'));
                        return;
                    }

                    texture.image = { width: texData.width, height: texData.height };
                    texture.mipmaps = texData.mipmaps;
                    if (texData.mipmapCount == 1)
                        texture.minFilter = LinearFilter;

                    texture.format = texData.format;
                    texture.needsUpdate = true;
                    texture.generateMipmaps = false;
                    texture.isCompressedTexture = true;
                    texture.flipY = false;

                    //console.log('Loaded texture: ' + url);
                }

                if (onLoad !== undefined)
                    onLoad(texture);
            },
            onProgress,
            function (error) {
                if (format == 'astc' && self.etc1Supported) {
                    // Fall back to etc1 if failed to load the first astc texture.
                    console.warn("ASTC texture not found, trying to load ETC1 instead...");
                    self.preferedFormat = 'etc1';
                    self.loadCompressedTexture(url, lod, texture, manager, self.preferedFormat, onLoad, onProgress, onError);
                }
                else {
                    console.warn("CompressedTextureLoader: failed to load texture: " + url, error);

                    if (onLoad !== undefined)
                        onLoad(null);
                }
            }
        );
    },

    loadLOD: function (lod) {
        var self = this;

        this.textureByFilenameMap.forEach(
            (texture, url, map) => {
                if (texture instanceof VideoTexture)
                    return;

                // Uncompressed textures don't have lods.
                if (!self.compressedTexturesEnabled || url.startsWith('images/'))
                    return;

                this.loadCompressedTexture(url, lod, texture, undefined, self.preferedFormat, undefined, undefined, undefined);
            });
    },

    setCrossOrigin: function (value) {
		this.crossOrigin = value;
		return this;
	},

    setPath: function (value) {
        this.path = value;
        if (compressedFileLoader)
            compressedFileLoader.setPath(value);        
		return this;
	}

} );

export { CompressedTextureLoader };