export default /* glsl */`
#define STANDARD
#define PHYSICAL
#define REFLECTIVITY
#define USE_UV

uniform vec3 diffuse;
uniform vec3 emissive;
uniform float roughness;
uniform float roughnessMapMin;
uniform float roughnessMapMax;
uniform float metalness;        
uniform float opacity;                
uniform float reflectivity;        

varying vec3 vViewPosition;

#ifndef FLAT_SHADED
    varying vec3 vNormal;

    #ifdef USE_TANGENT
        varying vec3 vTangent;
        varying vec3 vBitangent;
    #endif

#endif

#include <common>
#include <packing>
#include <dithering_pars_fragment>
#include <color_pars_fragment>
#include <uv_pars_fragment>
#include <uv2_pars_fragment>
#include <map_pars_fragment>
#include <alphamap_pars_fragment>
#include <aomap_pars_fragment>
#include <lightmap_pars_fragment>
#include <emissivemap_pars_fragment>
#include <bsdfs>
#include <cube_uv_reflection_fragment>        
#include <envmap_common_pars_fragment>        
#include <envmap_physical_pars_fragment>
#include <fog_pars_fragment>
#include <lights_pars_begin>
#include <lights_physical_pars_fragment>
#include <shadowmap_pars_fragment>
#include <bumpmap_pars_fragment>
#include <normalmap_pars_fragment>
#include <roughnessmap_pars_fragment>
#include <metalnessmap_pars_fragment>
#include <logdepthbuf_pars_fragment>
#include <clipping_planes_pars_fragment>


// Analytical approximation of the DFG LUT, one half of the
// split-sum approximation used in indirect specular lighting.
// via 'environmentBRDF' from "Physically Based Shading on Mobile"
// https://www.unrealengine.com/blog/physically-based-shading-on-mobile - environmentBRDF for GGX on mobile
vec3 EnvBRDFApprox(const in vec3 viewDir, const in vec3 normal, const in vec3 specularColor, const in float roughness)
{
    float dotNV = saturate(dot(normal, viewDir));

	// [ Lazarov 2013, "Getting More Physical in Call of Duty: Black Ops II" ]
    // Adaptation to fit our G term.
	const vec4 c0 = vec4(-1, -0.0275, -0.572, 0.022);
	const vec4 c1 = vec4(1, 0.0425, 1.04, - 0.04);
	vec4 r = roughness * c0 + c1;
	float a004 = min(r.x * r.x, exp2(-9.28 * dotNV)) * r.x + r.y;

    vec2 brdf = vec2(-1.04, 1.04) * a004 + r.zw;

	// Anything less than 2% is physically impossible and is instead considered to be shadowing
	// Note: this is needed for the 'specular' show flag to work, since it uses a SpecularColor of 0
	brdf.y *= saturate(50.0 * specularColor.g);

    return specularColor * brdf.x + brdf.y;
}

void main() 
{    
    #include <clipping_planes_fragment>

    vec4 diffuseColor = vec4( diffuse, opacity );
    ReflectedLight reflectedLight = ReflectedLight( vec3( 0.0 ), vec3( 0.0 ), vec3( 0.0 ), vec3( 0.0 ) );
    vec3 totalEmissiveRadiance = emissive;

    #include <logdepthbuf_fragment>
    #include <map_fragment>
    #include <color_fragment>
    #include <alphamap_fragment>
    #include <alphatest_fragment>            
    #include <metalnessmap_fragment>
    #include <normal_fragment_begin>	        
    #include <emissivemap_fragment>                            

    //#include <roughnessmap_fragment>
    float roughnessFactor = roughness;
    #ifdef USE_ROUGHNESSMAP
        vec4 texelRoughness = texture2D( roughnessMap, vUv );
        // Reads channel G, compatible with a combined OcclusionRoughnessMetallic (RGB) texture
        texelRoughness.g = mix(roughnessMapMin, roughnessMapMax, texelRoughness.g);                
        roughnessFactor *= texelRoughness.g;
    #endif
    
    //#include <normal_fragment_maps>
    #if defined TANGENTSPACE_NORMALMAP 
        vec3 mapN;
        #if defined COMPRESSED_NORMALMAP
            mapN.xy = texture2D(normalMap, vUv).wy * 2.0 - 1.0;
            mapN.z = sqrt(1.0 - dot(mapN.xy, mapN.xy));
        #else
            mapN = texture2D(normalMap, vUv).xyz * 2.0 - 1.0;
        #endif

        #if defined INVERT_NORMALMAP_Y
            mapN.y = -mapN.y;
        #endif

        mapN.xy *= normalScale;

        #ifdef USE_TANGENT
            normal = normalize(vTBN * mapN);
        #else
            normal = perturbNormal2Arb( -vViewPosition, normal, mapN );
        #endif
    #elif defined USE_BUMPMAP
        normal = perturbNormalArb( -vViewPosition, normal, dHdxy_fwd() );
    #endif

    // accumulation
    //#include <lights_physical_fragment>

    PhysicalMaterial material;

    vec3 dxy = max( abs( dFdx( geometryNormal ) ), abs( dFdy( geometryNormal ) ) );
    float geometryRoughness = max( max( dxy.x, dxy.y ), dxy.z );
    material.specularRoughness = max(roughnessFactor, 0.0525); // 0.0525 corresponds to the base mip of a 256 cubemap.
    material.specularRoughness += geometryRoughness;
    material.specularRoughness = min(material.specularRoughness, 1.0);    
    
    material.diffuseColor = diffuseColor.rgb - diffuseColor.rgb * metalnessFactor; // 1 mad
    float dielectricSpecular = 0.08 * reflectivity;	
    material.specularColor = (dielectricSpecular - dielectricSpecular * metalnessFactor) + diffuseColor.rgb * metalnessFactor;	// 2 mad            
    
//    #if defined BAKED_LIGHT_ONLY
        GeometricContext geometry;
        geometry.position = -vViewPosition;
        geometry.normal = normal;
        geometry.viewDir = normalize(vViewPosition);        
        vec3 radiance = vec3(0.0);
        vec3 irradiance = vec3(0.0);
        vec3 clearcoatRadiance = vec3(0.0);            
//    #else
//        #include <lights_fragment_begin>
//    #endif 
        
    //#include <lights_fragment_maps>
    #ifdef USE_LIGHTMAP	
        vec4 lightMapTexel = texture2D( lightMap, vUv2 );
        vec3 lightMap = lightMapTexelToLinear( lightMapTexel ).rgb * lightMapIntensity;            
		irradiance += lightMap - lightMap * metalnessFactor;
	#endif
    
    #if defined( USE_ENVMAP )
        radiance += getLightProbeIndirectRadiance(geometry.viewDir, geometry.normal, material.specularRoughness, maxMipLevel);
    #endif
    
    //#include <lights_fragment_end>

    // Both indirect specular and indirect diffuse light accumulate here
    reflectedLight.indirectDiffuse += irradiance;    
    reflectedLight.indirectSpecular += radiance * EnvBRDFApprox(geometry.viewDir, geometry.normal, material.specularColor, material.specularRoughness);

    // modulation
    #include <aomap_fragment>
    
    // this is a stub for the transparency model
    #ifdef TRANSPARENCY
        diffuseColor.a *= saturate( 1. - transparency + linearToRelativeLuminance( reflectedLight.directSpecular + reflectedLight.indirectSpecular ) );
    #endif            

    vec3 outgoingLight = reflectedLight.directDiffuse + reflectedLight.indirectDiffuse + reflectedLight.directSpecular + reflectedLight.indirectSpecular + totalEmissiveRadiance;             
    //vec3 outgoingLight = reflectedLight.indirectDiffuse;
    //vec3 outgoingLight = reflectedLight.directSpecular;
    //vec3 outgoingLight = reflectedLight.indirectSpecular;
    //vec3 outgoingLight = reflectedLight.directDiffuse + reflectedLight.indirectDiffuse;
    //vec3 outgoingLight = reflectedLight.directSpecular + reflectedLight.indirectSpecular;            

    gl_FragColor = vec4(outgoingLight, diffuseColor.a);
                
    #include <tonemapping_fragment>
    #include <encodings_fragment>
    #include <fog_fragment>
    #include <premultiplied_alpha_fragment>
    #include <dithering_fragment>
}
`;