import { MathUtils, Vector2, Vector3, Raycaster, Matrix3 } from 'three';
import { Component } from './Component';
import { BaseMovePointer, PointerState } from './BaseMovePointer';
import { OpenableDoor } from './OpenableDoor';

class MovePointer extends BaseMovePointer
{    
    constructor(domElement, scene, textureLoader)
    {
        super(scene, textureLoader);

        this.domElement = domElement;        
        this.mouseDown = false;
        this.touchIdentifier  = null;

        // Might be a mouse or a touch position.
        this.cursorPosition = new Vector2();
        this.mouseDownPosition = new Vector2();
        this.touchStartPosition = new Vector2();
        this.mouseDownAndMoved = false;
        this.touchStartedAndMoved = false;

        this.smoothedHitNormal = new Vector3();

        var self = this;

        function onMouseDown(e)
        {
            if (e.button != 0)
                return;
    
            self.cursorPosition.x = e.clientX;
            self.cursorPosition.y = e.clientY;        
            
            self.mouseDownPosition.copy(self.cursorPosition);        
            self.mouseDown = true;
            self.mouseDownAndMoved = false;
            self.pointerState = PointerState.Hidden;
        }

        function onMouseMove(e)
        {
            self.cursorPosition.x = e.clientX;
            self.cursorPosition.y = e.clientY;        
    
            if (self.mouseDown)
            {
                if (Math.abs(self.mouseDownPosition.x - self.cursorPosition.x) >= 2 && Math.abs(self.mouseDownPosition.y - self.cursorPosition.y) >= 2)
                    self.mouseDownAndMoved = true;
            }
    
            self.pointerState = PointerState.Visible;
        }
    
        function onMouseUp(e)
        {
            if (e.button != 0)
                return;
    
            if (!self.mouseDownAndMoved && self.enabled)
            {                                                 
                var door = Component.GetComponentInParent(self.hitObject, OpenableDoor.name);
                if (door)
                    door.openClose();                
                else
                {
                    if (self.computeTargetMovePoint())
                        self.domElement.dispatchEvent(self.moveEvent);
                }
            }
    
            self.mouseDown = false;
            self.mouseDownAndMoved = false;
        }
    
        function onTouchStart(e)
        {
            if (e.touches.length == 1)
            {
                if (!self.touchIdentifier)
                {
                    self.touchIdentifier = e.touches[0].identifier;
                    self.cursorPosition.x = e.touches[0].clientX;
                    self.cursorPosition.y = e.touches[0].clientY;
                    self.touchStartPosition.copy(self.cursorPosition);
                    self.touchStartedAndMoved = false;
                    self.pointerState = PointerState.Visible;
                }
            }
            else if (e.touches.length == 2) // To prevent move on two fingers touch
            {
                self.touchStartedAndMoved = true;
                self.pointerState = PointerState.Hidden;
                self.touchIdentifier = null;
            }
        }
    
        function onTouchMove(e)
        {
            e.preventDefault();
            e.stopPropagation();
    
            if (e.touches.length == 1 && e.touches[0].identifier == self.touchIdentifier)
            {
                self.cursorPosition.x = e.touches[0].clientX;
                self.cursorPosition.y = e.touches[0].clientY;
    
                if (Math.abs(self.touchStartPosition.x - self.cursorPosition.x) >= 4 && Math.abs(self.touchStartPosition.y - self.cursorPosition.y) >= 4)
                    self.touchStartedAndMoved = true;

                self.pointerState = PointerState.Hidden;
            }
        }
    
        function onTouchEnd(e)
        {
            for (var i=0; i<e.changedTouches.length; i++)
            {
                var t = e.changedTouches[i];
    
                if (self.touchIdentifier == t.identifier)
                {
                    if (!self.touchStartedAndMoved && self.enabled)
                    {
                        var door = Component.GetComponentInParent(self.hitObject, OpenableDoor.name);
                        if (door)
                        {
                            door.openClose();
                        }
                        else
                        {
                            if (self.computeTargetMovePoint())
                                self.domElement.dispatchEvent(self.moveEvent);
                        }
                    }

                    self.touchIdentifier = null;
                    self.touchStartedAndMoved = false;
                    self.pointerState = PointerState.Hidden;
                }
            }
        }

        domElement.addEventListener('mousemove', onMouseMove, false);
        domElement.addEventListener('mousedown', onMouseDown, false);
        domElement.addEventListener('mouseup', onMouseUp, false);
        domElement.addEventListener('touchstart', onTouchStart, false);
        domElement.addEventListener('touchend', onTouchEnd, false);
        domElement.addEventListener('touchmove', onTouchMove, false);
    }

    update(dt, camera)
    {
        super.update(dt, camera);
        
        this.pointsInteractable = false;
        this.hitObject = null;
        this.hitPoint = null;
        this.hitNormal = null;

        if (!this.enabled)
        {
            this.pointerObject.visible = false;
            this.pointerMaterial.opacity = 0.0;
            this.pointerState = PointerState.Hidden;
            return;
        }
        
        var projCursorPosition = new Vector2();
        projCursorPosition.x =  (this.cursorPosition.x / window.innerWidth) * 2 - 1;
        projCursorPosition.y = -(this.cursorPosition.y / window.innerHeight) * 2 + 1;

        var raycaster = new Raycaster();
        raycaster.setFromCamera(projCursorPosition, camera);
        
        var hit;
        var intersects = raycaster.intersectObject(this.scene, true);                  
        for (var i=0; i<intersects.length; i++)
        {
            var iobj = intersects[i].object;
            if (iobj != this.pointerObject)
            {
                hit = intersects[i];                
                break;
            }
        }

        if (!hit)
        {
            this.pointerObject.visible = false;
            this.pointerMaterial.opacity = 0.0;
            this.smoothedHitNormal.set(0,0,0);
            return;
        }

        var normalMatrix = new Matrix3();
        normalMatrix.getNormalMatrix(hit.object.matrixWorld);        

        this.hitObject = hit.object;
        this.hitPoint = hit.point.clone();
        this.hitNormal = hit.face.normal.clone().applyMatrix3(normalMatrix);        
        this.hitNormal.normalize();
                
        if (Component.GetComponentInParent(this.hitObject, OpenableDoor.name))
        {
            this.pointerMaterial.map = this.openDoorTexture;
            this.pointsInteractable = true;
        }
        else
        {
            this.pointerMaterial.map = this.pointerTexture;            
        }

        if (this.smoothedHitNormal.lengthSq() == 0 || this.pointerMaterial.opacity < 0.05)
            this.smoothedHitNormal.copy(this.hitNormal);
        else
            this.smoothedHitNormal.lerpVectors(this.smoothedHitNormal.clone(), this.hitNormal, 0.3);
        this.smoothedHitNormal.normalize();

        this.pointerObject.position.copy(this.hitPoint).add(this.smoothedHitNormal);
        this.pointerObject.lookAt(this.hitPoint.clone().add(this.smoothedHitNormal.clone().multiplyScalar(2.0)));
        this.pointerObject.visible = true;

        if (this.pointerState == PointerState.Visible)
            this.pointerMaterial.opacity += dt * 3.0;    // show
        else
            this.pointerMaterial.opacity -= dt * 3.0;
        this.pointerMaterial.opacity = MathUtils.clamp(this.pointerMaterial.opacity, 0.0, 1.0);
    }   
}

export { MovePointer };