class Component {    
    constructor() {
        this.sceneObject = null;
    }

    static AddComponent(obj, component) {
        if (obj == null)
            throw `obj can't be null`;
        if (component == null)
            throw `component can't be null`;

        component.sceneObject = obj;

        if (!obj.components)
            obj.components = [];
        obj.components.push(component);

        component.start();
    }

    static GetComponent(obj, className) {
        if (!obj.components)
            return null;
        for (var i=0; i<obj.components.length; i++) {            
            if (obj.components[i].constructor.name == className)
                return obj.components[i];
        }

        return null;
    }

    static GetComponentInParent(obj, className) {        
        while (obj) {
            var c = Component.GetComponent(obj, className);
            if (c)
                return c;
            obj = obj.parent;
        }    

        return null;
    }

    static update(root, dt) {
        root.traverse(function (obj) {
            if (!obj.components)
                return;
            for (var i=0; i<obj.components.length; i++)
                obj.components[i].update(dt);
        });    
    }

    start() {        
    }

    update(dt) {
    }
}

export { Component };