import { Vector3 } from 'three';
import { playerInstance } from './Player';

var ButtonObjectType = {
    Marker: 1,
    Waypoint: 2
};

var globalButtonIndex = 0;

var ButtonObject = function (object, parentContainer, buttonType, text, classId, iconId, iconText, onClick) {
    this.buttonType = buttonType;

    var hideForced = false;
    var currentOpacity = 1.0;
    var targetOpacity = 1.0;
    var frameNumber = 0;
    var buttonIndex = globalButtonIndex++;

    var domElement = document.createElement("div");

    if (buttonType === ButtonObjectType.Marker) {
        if (classId === 'shortest-text') {
            domElement.className = 'marker-button';
            domElement.innerHTML += `<svg class="marker-button-shortest-text-box" viewBox="0 0 32 32"></svg>`;

            if (iconText !== undefined)
                domElement.innerHTML += `<div class="marker-button-shortest-text">${iconText}</div>`;
        }
        else if (classId === 'icon-expandable-text' || classId === 'icon-expandable-text-small') {            
            if (iconId === undefined) {
                iconId = 'room';
                console.warn(`"iconId" field must be defined for markers of classId = "${classId}"`);
            }

            if (text == undefined) {
                text = "";                
                console.warn(`"text" field must be defined for markers of classId = "${classId}"`);
            }        

            if (classId === 'icon-expandable-text-small') {
                domElement.className = 'marker-button-small';
                domElement.innerHTML += `<svg class="marker-button-icon-box-small" viewBox="0 0 32 32">
                    <use class="marker-button-icon" xlink:href="#${iconId}"></use>
                    </svg>`;
            }
            else {
                domElement.className = 'marker-button';
                domElement.innerHTML += `<svg class="marker-button-icon-box" viewBox="0 0 32 32">
                    <use class="marker-button-icon" xlink:href="#${iconId}"></use>
                    </svg>`;
            }

            var textElem = document.createElement("span");
            textElem.innerHTML = text;
            textElem.className = classId === 'icon-expandable-text-small' ? 'marker-button-text-small' : 'marker-button-text';

            domElement.appendChild(textElem);
        }
        else if (classId == 'icon-expandable-multiline-text') {
            domElement.className = 'marker-button-expandable-multiline';

            if (iconId === undefined) {
                iconId = 'room';
                console.warn(`"iconId" field must be defined for markers of classId = "${classId}"`);
            }

            if (text == undefined) {
                text = "";                
                console.warn(`"text" field must be defined for markers of classId = "${classId}"`);
            }
        
            domElement.innerHTML += `<svg class="marker-button-expandable-multiline-icon-box" viewBox="0 0 32 32">
                <use class="marker-button-expandable-multiline-icon" xlink:href="#${iconId}"></use>
                </svg>`;

            var textElem = document.createElement("span");
            textElem.innerHTML = text;
            textElem.className = 'marker-button-expandable-multiline-text';

            domElement.appendChild(textElem);
        }        
        else if (classId == 'panoramic-view-button') {
            domElement.className = 'marker-button';
            
            if (iconId === undefined) {        
                var room = text.match(/^[а-яА-Я]+\.? ?[а-яА-Я]+/);
                if (room) {
                    switch (room[0])
                    {
                        case 'Сан. узел':
                            iconId = 'bathroom';
                            break;
                        case 'Кухня':
                            iconId = 'kitchen';
                            break;
                        case 'Детская':
                            iconId = 'nursery';
                            break;
                        case 'Коридор':
                            iconId = 'hallway';
                            break;
                        case 'Спальня':
                            iconId = 'bedroom';
                            break;
                        default:
                            iconId = 'room';
                            break;
                    }
                }
            }

            domElement.innerHTML += `<svg class="marker-button-icon-box" viewBox="0 0 32 32">
                <use class="marker-button-icon" xlink:href="#${iconId}"></use>
                </svg>`;

            var textElem = document.createElement("span");
            textElem.innerHTML = text;
            textElem.className = 'marker-button-text';

            domElement.appendChild(textElem);
        }
    }
    // Эспериментальная возможность, waypoint не используется.
    else if (buttonType == ButtonObjectType.Waypoint) {
        domElement.className = 'waypoint-button';
    }
    domElement.onclick = click;

    parentContainer.appendChild(domElement);

    this.update = function (scene, renderer, camera) {
        frameNumber++;    

        if (hideForced)            
            return;

        domElement.style.visibility = "visible";

        var worldPosition = new Vector3();
        object.getWorldPosition(worldPosition);

        var cameraWorldPosition = new Vector3();
        camera.getWorldPosition(cameraWorldPosition);

        var position = new Vector3();
        position.copy(worldPosition);
        position.project(camera);

        const padding = 0;
        position.x = (position.x + 1.0) * 0.5 * (window.innerWidth - padding);
        position.y = (1.0 - position.y) * 0.5 * (window.innerHeight);        

        if (position.z <= 0 || position.z >= 1) {
            domElement.style.opacity = 0;
            return;
        }

        var rect = domElement.getBoundingClientRect();
        domElement.style.left = `${position.x - rect.width/2}px`;
        domElement.style.top = `${position.y - rect.height/2}px`;

        // Прячем иконку, если ее загораживают объекты.
        // Optimization: raycast one button per frame
        if ((frameNumber + buttonIndex) % globalButtonIndex == 0) {            
            var intersects;
            var bsp = playerInstance.getBspTree();            
            //var start = Date.now();                       
            //for (var k=0; k<1000; k++)
                intersects = bsp.rayIntersect(cameraWorldPosition, worldPosition);
            //var end = Date.now();
            //console.log(end-start);                            
            if (intersects.length > 0) {
                var totalOpacity = 1.0;

                for (var i = 0; i < intersects.length; i++) {
                    var intersect = intersects[i];
                    var mtlIndex = intersect.materialIndex;

                    if (Array.isArray(intersect.object.material))
                        totalOpacity *= 1.0 - intersect.object.material[mtlIndex].opacity;
                    else
                        totalOpacity *= 1.0 - intersect.object.material.opacity;
                }

                targetOpacity = totalOpacity;
            }
            else {
                targetOpacity = 1.0; 
            }
        }

        var t = 0.3;
        currentOpacity = currentOpacity * (1 - t) + targetOpacity * t;

        domElement.style.opacity = currentOpacity;
    }

    this.hide = function () {
        domElement.style.visibility = "hidden";
        hideForced = true;
    }

    this.show = function ()  {        
        hideForced = false;
    }

    function click() {
        if (onClick)
            onClick(object);
    }
}

export { ButtonObject, ButtonObjectType };