import { PlaneBufferGeometry, Line3, Mesh, MeshBasicMaterial, Plane, Vector3, Raycaster } from 'three';
import { Component } from './Component';
import { playerInstance } from './Player';

var PointerState = {
    Visible: 0,
    Hidden: 1
};

class BaseMovePointer
{       
    constructor(scene, textureLoader)
    {                
        this.scene = scene;
        this.textureLoader = textureLoader;
        this.navMesh = null;
        this.enabled = false;                
        this.pointsInteractable = false;
        this.movePoint = null;
        this.hitObject = null;
        this.hitPoint = null;
        this.hitNormal = null;
        this.pointerState = PointerState.Visible;        
        this.moveEvent = new CustomEvent("movetopointer");
    
        // Pointer

        var pointerGeometry = new PlaneBufferGeometry(25, 25, 1, 1);        
        this.pointerTexture = textureLoader.load('images/move_pointer.png');
        this.pointerTexture.anisotropy = 8;
        this.pointerTexture.generateMipmaps = true;
        this.openDoorTexture = textureLoader.load('images/open_door_pointer.png');        
        this.openDoorTexture.anisotropy = 8;
        this.openDoorTexture.generateMipmaps = true;

        this.pointerMaterial = new MeshBasicMaterial( { 
            map: this.pointerTexture,
            opacity: 0.0,
            transparent: true,
            depthTest: false
        } );

        this.pointerObject = new Mesh(pointerGeometry, this.pointerMaterial);
        this.pointerObject.renderOrder = 10001; // render after all other objects
        this.pointerObject.visible = this.enabled;
        scene.add(this.pointerObject);    
    }

    update(dt, camera)
    {        
    }

    // Точка, в которую может перейти наблюдатель. Значение валидно только после вызова computeTargetMovePoint()
    getTargetMovePoint()
    {
        if (this.movePoint)
            return this.movePoint.clone();
        return undefined;
    }

    computeTargetMovePoint()
    {
        this.movePoint = null;

        // Если нет NavMesh в сцене, то точка, куда наведен курсор, является точкой для пермещения.
        if (!this.navMesh)
        {
            this.movePoint = this.hitPoint.clone().add(hitNormal);
            return true;
        }

        var camera = playerInstance.getCurrentCamera();
        if (!camera)
        {            
            console.error('getCurrentCamera() returned is null');
            return false;
        }

        var cameraPosition = new Vector3();
        camera.getWorldPosition(cameraPosition);        

        // Трассировка вертикального луча для поиска пересечения с navmesh.
        var raycaster = new Raycaster();
        raycaster.set(this.hitPoint.clone().add(new Vector3(0,10000,0)), new Vector3(0,-1,0));
        var navMeshIntersects = [];
        this.navMesh.raycast(raycaster, navMeshIntersects);

        //var navMeshNormalMatrix = new Matrix3();
        //navMeshNormalMatrix.getNormalMatrix(this.navMesh.matrixWorld);

        // Если курсор наведен на navmesh, разрешаем движение.
        if (navMeshIntersects.length > 0)
        {
            var h = navMeshIntersects[0];                
            this.movePoint = h.point;        
            return true;            
        }

        // Если не можем двинуться в точку курсора, то ищем ближайшую к нему точку navmesh-а.
        var hitPlane = new Plane();
        hitPlane.setFromNormalAndCoplanarPoint(this.hitNormal, this.hitPoint);
        var navMeshClosestPoint = this.findNavMeshClosestPoint(this.hitPoint, hitPlane);
         // Если ничего не найдено, то ищем без плоскости отсечения.
        if (!navMeshClosestPoint)
            navMeshClosestPoint = this.findNavMeshClosestPoint(this.hitPoint);
        if (navMeshClosestPoint)
        {
            // Трассируем луч от камеры к точке на навмеше, чтобы определить
            // есть ли препятствие перед нами.                        
            var dir = navMeshClosestPoint.clone().sub(cameraPosition);
            var dirlen = dir.length();
            dir.divideScalar(dirlen);

            raycaster.set(cameraPosition, dir);
            var intersects = raycaster.intersectObject(this.scene, true);          
                        
            for (var i=0; i<intersects.length; i++)
            {
                var iobj = intersects[i].object;
                if (iobj != this.pointerObject && iobj != this.navMesh && intersects[i].distance < dirlen)
                {                    
                    return false;
                }
            }

            // Ни с чем не пересеклись, движение разрешено.            
            this.movePoint = navMeshClosestPoint;
            return true;            
        }        

        return false;
    }    

    findNavMeshClosestPoint(sourcePoint, plane)
    {
        var mesh = this.navMesh;        
        var geometry = mesh.geometry;

        var line = new Line3();
        var pointOnLine = new Vector3();
        
        var closestPointDistance = Number.MAX_VALUE;
        var closestPoint = new Vector3();        

        var vA = new Vector3();
        var vB = new Vector3();
        var vC = new Vector3();

        if (geometry.isBufferGeometry) 
        {
            var index = geometry.index;
            var position = geometry.attributes.position;
            
            var indexCount = index ? index.count : position.count;

            for (var j=0; j < indexCount; j += 3) 
            {
                var a = index ? index.getX(j )    : j;
                var b = index ? index.getX(j + 1) : j + 1;
                var c = index ? index.getX(j + 2) : j + 2;
        
				vA.fromBufferAttribute(position, a);
				vB.fromBufferAttribute(position, b);
                vC.fromBufferAttribute(position, c);

                // Transform to world space.
                vA.applyMatrix4(mesh.matrixWorld);
                vB.applyMatrix4(mesh.matrixWorld);
                vC.applyMatrix4(mesh.matrixWorld);

                // Точка на навмеше должна быть с положительной стороны заданной плоскости.
                if (plane)
                {
                    if (plane.distanceToPoint(vA) < 0 &&
                        plane.distanceToPoint(vB) < 0 &&
                        plane.distanceToPoint(vC) < 0)
                        continue;
                }
                                             
                var dist;

                // A-B
                line.start = vA;
                line.end = vB;            
                line.closestPointToPoint(sourcePoint, true, pointOnLine);                
                dist = sourcePoint.distanceToSquared(pointOnLine);
                if (dist < closestPointDistance)
                {
                    closestPoint.copy(pointOnLine);                    
                    closestPointDistance = dist;
                }

                // B-C
                line.start = vB;
                line.end = vC;            
                line.closestPointToPoint(sourcePoint, true, pointOnLine);                
                dist = sourcePoint.distanceToSquared(pointOnLine);
                if (dist < closestPointDistance)
                {
                    closestPoint.copy(pointOnLine);                    
                    closestPointDistance = dist;
                }            

                // C-A
                line.start = vC;
                line.end = vA;            
                line.closestPointToPoint(sourcePoint, true, pointOnLine);                
                dist = sourcePoint.distanceToSquared(pointOnLine);
                if (dist < closestPointDistance)
                {
                    closestPoint.copy(pointOnLine);
                    closestPointDistance = dist;
                }
            }
        }

        if (closestPointDistance < Number.MAX_VALUE)
            return closestPoint;
        else
            return null;
    }

    openCloseDoor(obj)
    {

    }

    isOpenableDoor(obj)
    {
        return Component.GetComponent(obj, 'OpenableDoor') != null;        
    }
}

export { BaseMovePointer, PointerState };